import React, { Component } from 'react';

import {Api} from './Api'
import {Navegador, NavBar} from './Componentes'
import {rotas, menu} from './configuracoes'
import { isEqual } from "lodash";


export default class App extends Component {
    api = new Api()

    state = {}

    componentWillMount(){
        this.interval = setInterval(
            () => {
                const curr = {
                    accessTokenAtivo : this.api.Auth.accessTokenAtivo(),
                    refreshTokenAtivo : this.api.Auth.refreshTokenAtivo()
                }

                if(!isEqual(curr, this.state)) {
                    this.setState({
                        accessTokenAtivo : this.api.Auth.accessTokenAtivo(),
                        refreshTokenAtivo : this.api.Auth.refreshTokenAtivo()
                    })
                }
            },
            300
        )
    }

    componentWillUnmount(){
        clearInterval(this.interval)
    }

    render() {
        const {accessTokenAtivo, refreshTokenAtivo} = this.state

        const logado = accessTokenAtivo

        if(!accessTokenAtivo && refreshTokenAtivo){
            this.api.Auth.Refresh()
        }

        else if (!accessTokenAtivo && !refreshTokenAtivo){
            window.location.href = '#login'
        }

        return (
            <div className="container bg-white p-0" style={{minHeight:"100vh", minWidth: "90%"}}>
                {logado ? <NavBar menu={menu}/> : null}
                <Navegador rotas={rotas} api={this.api}/>
            </div>
        )
    }
}
