import React, { Component } from 'react';

export default class ListarConcedentesPropostos extends Component {
    constructor(){
        super()
        this.state = {
            listaDePropostas : []
        }

        this.carregarListaDePropostas = this.carregarListaDePropostas.bind(this)
        this.efetivarProposta = this.efetivarProposta.bind(this)
        this.apagarProposta = this.apagarProposta.bind(this)
    }

    componentWillMount(){
        this.carregarListaDePropostas()
    }

    carregarListaDePropostas(){
        const {api} = this.props
        api.Concedentes.ListaPropostas(
            (listaDePropostas) => this.setState({listaDePropostas}),
            (erro) => console.log('erro',erro)
        )
    }

    efetivarProposta(proposta){
        const {api} = this.props

        const concedente = {
            ...proposta,
            identificacao_cadastral : proposta.identificacao_cadastral.codigo_de_cadastro,
            ativo : true
        }

        api.Concedentes.AtualizaConcedente(concedente.id, concedente, 
            window.location.href = " #",
            (erro) => console.log('erro',erro)
        )
    }

    apagarProposta(id){
        const {api} = this.props

        api.Concedentes.RemovePropostaPelaId(id,
            this.setState(prevState => {
                const listaDePropostas = prevState.listaDePropostas.filter(proposta => proposta.id !== id)
                return {listaDePropostas}
            }),
            (erro) => console.log('erro',erro)
        )
    }

    linkParaBaixarAcordoDeCooperacao(id){
        const {api} = this.props
        return `${api.Concedentes.getBaseUrl()}/${id}/baixar`
    }

    baixar(id){
        const {api} = this.props
        const token = api.Auth.accessToken()
        const link = this.linkParaBaixarAcordoDeCooperacao(id)

        fetch(link, {
            headers : {
                "Authorization" : `Bearer ${token}`
            }
        })
        .then(res => res.blob())
        .then(blob => {
            const blobUrl = window.URL.createObjectURL(new Blob([blob]));

            this.setState(prevState => {
                const {listaDePropostas} =  prevState
                
                const proposta = listaDePropostas.filter(proposta => proposta.id === id)[0]
                const propostaAtualizada = {...proposta, blobUrl}

                const listaDePropostasAtualizada = listaDePropostas.reduce( (listaNova, valorOriginal) => {
                    listaNova.push( valorOriginal.id === id ? propostaAtualizada : valorOriginal )
                    return listaNova
                }, [])

                return ({
                    ...prevState,
                    listaDePropostas : [...listaDePropostasAtualizada]
                })
            })
        })
    }

    render() {
        const {listaDePropostas} = this.state

        return (
            <div>
                <h5>Rela√ß√£o de Propostas para Concedente</h5>
                <table className="table table-striped table-hover">
                    <thead>
                        <tr>
                            <th>Ident. Cadastral</th>
                            <th>Nome</th>
                            <th>Ramo de Atividade</th>
                            <th>Telefone</th>
                            <th>Email</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                    {
                        listaDePropostas.map(proposta => (
                            <tr key={proposta.id} title={proposta.observacoes}>
                                <td>
                                    {proposta.identificacao_cadastral.tipo_de_cadastro ? `${proposta.identificacao_cadastral.tipo_de_cadastro}: ` : <></>}
                                    {proposta.identificacao_cadastral.codigo_de_cadastro}
                                </td>
                                <td>{proposta.nome}</td>
                                <td>{proposta.ramo_atividade}</td>
                                <td>{proposta.telefone}</td>
                                <td>{proposta.email}</td>
                                <td>
                                {
                                    proposta.blobUrl === undefined ?
                                        <button onClick={() => this.baixar(proposta.id)}>Baixar</button>
                                    :
                                        <button onClick={() => window.open(proposta.blobUrl, '_blank')}>Abrir</button>
                                }
                                    <button onClick={() => this.efetivarProposta(proposta)}>Efetivar</button>
                                    <button onClick={() => this.apagarProposta(proposta.id)}>Apagar</button>
                                </td>
                            </tr>
                        ))
                    }
                    </tbody>
                </table>
            </div>
        )
    }
}
