import React, { Component } from 'react';

export default class ListarConcedentes extends Component {
    constructor(){
        super()
        this.state = {
            listaDeConcedentes : []
        }

        this.carregarListaDeConcedentes = this.carregarListaDeConcedentes.bind(this)
    }

    componentWillMount(){
        this.carregarListaDeConcedentes()
    }

    carregarListaDeConcedentes(){
        const {api} = this.props
        api.Concedentes.ListaConcedentes(
            (listaDeConcedentes) => this.setState({listaDeConcedentes}),
            (erro) => console.log('erro',erro)
        )
    }

    linkParaBaixarAcordoDeCooperacao(id){
        const {api} = this.props
        return `${api.Concedentes.getBaseUrl()}/${id}/baixar`
    }

    baixar(id){
        const {api} = this.props
        const token = api.Auth.accessToken()
        const link = this.linkParaBaixarAcordoDeCooperacao(id)

        fetch(link, {
            headers : {
                "Authorization" : `Bearer ${token}`
            }
        })
        .then(res => res.blob())
        .then(blob => {
            const blobUrl = window.URL.createObjectURL(new Blob([blob]));

            this.setState(prevState => {
                const {listaDeConcedentes} =  prevState
                
                const concedente = listaDeConcedentes.filter(concedente => concedente.id === id)[0]
                const concedenteAtualizado = {...concedente, blobUrl}

                const listaDeConcedentesAtualizada = listaDeConcedentes.reduce( (listaNova, valorOriginal) => {
                    listaNova.push( valorOriginal.id === id ? concedenteAtualizado : valorOriginal )
                    return listaNova
                }, [])

                return ({
                    ...prevState,
                    listaDeConcedentes : [...listaDeConcedentesAtualizada]
                })
            })
        })
    }

    render() {
        const {listaDeConcedentes} = this.state

        return (
            <div>
                <h5>Rela√ß√£o de Concedentes</h5>
                <table className="table table-striped table-hover">
                    <thead>
                        <tr>
                            <th>Ident. Cadastral</th>
                            <th>Nome</th>
                            <th>Ramo de Atividade</th>
                            <th>Telefone</th>
                            <th>Email</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                    {
                        listaDeConcedentes.map(concedente => (
                            <tr key={concedente.id}>
                                <td>
                                    {concedente.identificacao_cadastral.tipo_de_cadastro ? `${concedente.identificacao_cadastral.tipo_de_cadastro}: ` : <></>}
                                    {concedente.identificacao_cadastral.codigo_de_cadastro}
                                </td>
                                <td>{concedente.nome}</td>
                                <td>{concedente.ramo_atividade}</td>
                                <td>{concedente.telefone}</td>
                                <td>{concedente.email}</td>
                                <td>
                                {
                                    concedente.blobUrl === undefined ?
                                        <button onClick={() => this.baixar(concedente.id)}>Baixar</button>
                                    :
                                        <button onClick={() => window.open(concedente.blobUrl, '_blank')}>Abrir</button>
                                }
                                </td>
                            </tr>
                        ))
                    }
                    </tbody>
                </table>
            </div>
        )
    }
}
