import React, { Component } from 'react';

import {Api} from '../../../Api'

export default class EmitirTCE extends Component {
    api = new Api()

    constructor(){
        super()
        this.state = {
            listaDeTCE : []
        }
    }

    componentDidMount(){
        this.api.TermosDeCompromisso.ListaTermosDeCompromisso(
            (listaDeTCE) => this.setState({listaDeTCE}),
            (erro) => console.log('erro',erro)
        )
    }

    linkParaBaixarTermo(registroCompleto = ''){
        const {api} = this.props
        
        const divAno = registroCompleto.lastIndexOf('/')

        const registro = registroCompleto.substr(0, divAno-1) + registroCompleto.substr(divAno)

        return `${api.TermosDeCompromisso.getBaseUrl()}/${registro}/baixar`
    }

    baixar(registro){
        const {api} = this.props
        const token = api.Auth.accessToken()
        const link = this.linkParaBaixarTermo(registro)

        fetch(link, {
            headers : {
                "Authorization" : `Bearer ${token}`
            }
        })
        .then(res => res.blob())
        .then(blob => {
            const blobUrl = window.URL.createObjectURL(new Blob([blob]));

            this.setState(prevState => {
                const {listaDeTCE} =  prevState
                
                const termo = listaDeTCE.filter(termo => termo.registro === registro)[0]
                const termoAtualizado = {...termo, blobUrl}

                const listaDeTCEAtualizada = listaDeTCE.reduce( (listaNova, valorOriginal) => {
                    listaNova.push( valorOriginal.registro === registro ? termoAtualizado : valorOriginal )
                    return listaNova
                }, [])

                return ({
                    ...prevState,
                    listaDeTCE : [...listaDeTCEAtualizada]
                })
            })
        })
    }

    render() {
        const {listaDeTCE} = this.state

        return (
            <div>
                <h5>Listar TCE</h5>
                <table className="table table-striped table-hover">
                    <thead>
                        <tr>
                            <th>Registro</th>
                            <th>Estagi√°rio</th>
                            <th>Campo de Est√°gio</th>
                            <th>Vig√™ncia</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                    {
                        listaDeTCE.map(termo => {
                            const { vigencia, aluno, local_do_estagio } = termo

                            return (
                                <tr key={termo.id}>
                                    <td>{termo.registro}</td>
                                    <td>{aluno.nome}</td>
                                    <td>{local_do_estagio.nome}</td>
                                    <td>
                                    { `${new Date(vigencia.inicio).toLocaleDateString()} a ${new Date(vigencia.fim).toLocaleDateString()}` }
                                    </td>
                                    <td>
                                        {
                                            termo.blobUrl === undefined ?
                                                <button onClick={() => this.baixar(termo.registro)}>Baixar</button>
                                            :
                                                <button onClick={() => window.open(termo.blobUrl, '_blank')}>Abrir</button>
                                        }
                                        <button onClick={() => this.aditivarTermo(termo)}>Aditivar</button>
                                    </td>
                                </tr>
                            )
                        })
                    }
                    </tbody>
                </table>
            </div>
        )
    }
}
