import React, { Component } from 'react';

export default class ListarCamposDeEstagio extends Component {
    constructor(){
        super()
        this.state = {
            listaDeCampos : []
        }

        this.carregarListaDeCampos = this.carregarListaDeCampos.bind(this)
    }

    componentWillMount(){
        this.carregarListaDeCampos()
    }

    carregarListaDeCampos(){
        const {api} = this.props
        api.CamposDeEstagio.ListaTodos(
            (listaDeCampos) => this.setState({listaDeCampos}),
            (erro) => console.log('erro',erro)
        )
    }

    linkParaBaixarModeloDeAteste(id){
        const {api} = this.props
        return `${api.CamposDeEstagio.getBaseUrl()}/${id}/baixar`
    }

    baixar(id){
        const {api} = this.props
        const token = api.Auth.accessToken()
        const link = this.linkParaBaixarModeloDeAteste(id)

        fetch(link, {
            headers : {
                "Authorization" : `Bearer ${token}`
            }
        })
        .then(res => res.blob())
        .then(blob => {
            const blobUrl = window.URL.createObjectURL(new Blob([blob]));

            this.setState(prevState => {
                const {listaDeCampos} =  prevState
                
                const campo = listaDeCampos.filter(campo => campo.id === id)[0]
                const campoAtualizado = {...campo, blobUrl}

                const listaDeCamposAtualizada = listaDeCampos.reduce( (listaNova, valorOriginal) => {
                    listaNova.push( valorOriginal.id === id ? campoAtualizado : valorOriginal )
                    return listaNova
                }, [])

                return ({
                    ...prevState,
                    listaDeCampos : [...listaDeCamposAtualizada]
                })
            })
        })
    }

    render() {
        const {listaDeCampos} = this.state

        return (
            <div>
                <h5>Rela√ß√£o de Campos de Est√°gio</h5>
                <table className="table table-striped table-hover">
                    <thead>
                        <tr>
                            <th>Concedente</th>
                            <th>Nome</th>
                            <th>Telefone</th>
                            <th>Email</th>
                            <th>Ateste (formul√°rio)</th>
                        </tr>
                    </thead>
                    <tbody>
                    {
                        listaDeCampos.map(campo => {
                            const {concedente} = campo

                            return (
                                <tr key={campo.id}>
                                    <td>{ concedente !== null ? concedente.nome : ''}</td>
                                    <td>{campo.nome}</td>
                                    <td>{campo.telefone}</td>
                                    <td>{campo.email}</td>
                                    <td>
                                        {
                                            campo.blobUrl === undefined ?
                                                <button onClick={() => this.baixar(campo.id)}>Baixar</button>
                                            :
                                                <button onClick={() => window.open(campo.blobUrl, '_blank')}>Abrir</button>
                                        }
                                    </td>
                                </tr>
                            )
                        })
                    }
                    </tbody>
                </table>
            </div>
        )
    }
}
