import React, { Component } from 'react';

export default class ListarAtestesDeCampo extends Component {
    constructor(){
        super()
        this.state = {
            listaDeAtestes : []
        }

        this.carregarListaDeAtestes = this.carregarListaDeAtestes.bind(this)
    }

    componentWillMount(){
        this.carregarListaDeAtestes()
    }

    carregarListaDeAtestes(){
        const {api} = this.props
        api.AtestesDeCampo.ListaTodos(
            (listaDeAtestes) => this.setState({listaDeAtestes}),
            (erro) => console.log('erro',erro)
        )
    }

    apagarAteste(id){
        const {api} = this.props

        api.AtestesDeCampo.RemovePelaId(id,
            this.setState(prevState => {
                const listaDeAtestes = prevState.listaDeAtestes.filter(ateste => ateste.id !== id)
                return {listaDeAtestes}
            }),
            (erro) => console.log('erro',erro)
        )
    }

    render() {
        const {listaDeAtestes} = this.state

        return (
            <div>
                <h5>Rela√ß√£o de Atestes de Campo</h5>
                <table className="table table-striped table-hover">
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th>Campo de Est√°gio</th>
                            <th>Respons√°vel</th>
                            <th>Curso</th>
                            <th>Vig√™ncia</th>
                            <th/>
                        </tr>
                    </thead>
                    <tbody>
                    {
                        listaDeAtestes.map((ateste, i) => {
                            const {responsavel, local_de_estagio, curso, vigencia} = ateste

                            return (
                                <tr key={ateste.id} title={ateste.observacoes}>
                                    <td>{ateste.ativo && <>{">"}</> }</td>
                                    <td>{i+1}</td>
                                    <td>{local_de_estagio.nome}</td>
                                    <td>{responsavel.nome}</td>
                                    <td>{curso.nome}</td>
                                    <td>
                                    { `${new Date(vigencia.inicio).toLocaleDateString()} a ${new Date(vigencia.fim).toLocaleDateString()}`}
                                    </td>
                                    <td>
                                        <button onClick={() => this.apagarAteste(ateste.id)}>Apagar</button>
                                    </td>
                                </tr>
                            )
                        })
                    }
                    </tbody>
                </table>
            </div>
        )
    }
}
